

































































































































































@import "../../../styles/variables";

.alpheios-principal-parts__lemma_index,
.alpheios-principal-parts__lemma_index_spacer {
  display: inline-block;
  text-align: center;
  font-weight: 700;
  margin-right: textsize(5px);
}

.alpheios-principal-parts__groupitem--lemma-word__listitem:not(:last-child) {
  margin-right: 3px;

  &:after {
    content: " - ";
  }
}

.alpheios-principal-parts__listitem:not(:last-child) {
  margin-right: 3px;

  &:after {
    content: ", ";
  }
}

.alpheios-principal-parts__item h4.alpheios-principal-parts__groupitem {
  display: flex;
  font-weight: 700;
  margin-right: 5px;
  margin-bottom: 0;
}

h4.alpheios-principal-parts__groupitem:last-of-type {
    margin-right: 0;

    // Comment the following rule out if colons are not required after headword(s)
    &:after {
        content: ':';
        margin-left: 3px;
    }
}

.alpheios-principal-parts__item {
    display: flex;

    .feature_extras {
        margin-left: 5px;
    }

    .feature_source {
        margin-left: 5px;
    }
}

.alpheios-morph-data__chinese p {
  margin-bottom: 0;
}

.alpheios-morph__attr i {
  font-style: italic;
  font-family: sans-serif;
  font-size: 90%;
}

.alpheios-principal-parts__pointer {
    &-tooltip {
        display: block;
        height: 22px;
        margin-left: -5px;
    }

    &-icn {
        display: block;
        fill: var(--alpheios-color-vivid);
        height: 22px;
    }
}

.alpheios-principal-parts__dsmbg-providers {
    display: block;
    height: 22px;

    &-tooltip {
      margin-left: 5px;
    }

    &-icn {
        fill: var(--alpheios-color-neutral-dark);
        display: block;
        height: 22px;
    }
}
