





































































@import "../../styles/variables";

.alph_tooltip {
  position: relative;
  display: inline-block;

  span.alpheios-tooltiptext.alpheios-tooltiptext-span {
    visibility: hidden;
    position: absolute;
    width: uisize(120px);
    padding: uisize(5px);
    background-color: var(--alpheios-text-bg-color);
    color: var(--alpheios-text-color);
    border: 1px solid var(--alpheios-border-color);
    border-radius: uisize(10px);
    z-index: 10;
    opacity: 0;
    transition: opacity .6s;
    display: none;
    font-size: textsize(12px);
    text-align: center;
  }
}

.alph_tooltip:hover span.alpheios-tooltiptext.alpheios-tooltiptext-span {
  visibility: visible;
  opacity: 1;
  display: inline;
}

.alph_tooltip-top {
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
}

.alph_tooltip-top::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: var(--alpheios-border-color) transparent transparent transparent;
}

.alph_tooltip-top-right {
  bottom: 125%;
  right: 0;
  margin-left: -50%;
}

.alph_tooltip-top-right::after {
  content: "";
  position: absolute;
  top: 100%;
  right: 15%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: var(--alpheios-border-color) transparent transparent transparent;
}

.alph_tooltip-top-left {
  bottom: 125%;
  left: 0;
}

.alph_tooltip-top-left::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  margin-left: 5px;
  border-width: 5px;
  border-style: solid;
  border-color: var(--alpheios-border-color) transparent transparent transparent;
}

.alph_tooltip-right {
  top: 50%;
  transform: translateY(-50%);
  left: 125%;
}

.alph_tooltip-right::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent var(--alpheios-border-color) transparent transparent;
}

.alph_tooltip-bottom {
  top: 135%;
  left: 50%;
  margin-left: -50%;
}

.alph_tooltip-bottom-wide {
  top: 135%;
  left: -30%;
}

.alph_tooltip-bottom-narrow {
  top: 135%;
  left: -75%;
}

.alph_tooltip-bottom-narrow2 {
  top: 135%;
  left: -165%;
}

.alph_tooltip-bottom::after,
.alph_tooltip-bottom-wide::after,
.alph_tooltip-bottom-narrow::after,
.alph_tooltip-bottom-narrow2::after  {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent var(--alpheios-border-color) transparent;
}

.alph_tooltip-bottom-right {
  top: 135%;
  right: 0;
  margin-left: -50%;
}

.alph_tooltip-bottom-right::after {
  content: "";
  position: absolute;
  bottom: 100%;
  right: 15%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent var(--alpheios-border-color) transparent;
}

.alph_tooltip-bottom-left {
  top: 135%;
  right: 0;
  left: auto;
}

.alph_tooltip-bottom-left::after {
  content: "";
  position: absolute;
  bottom: 100%;
  right: 15%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent var(--alpheios-border-color) transparent;
}

.alpheios-panel--right {
  .alph_tooltip-bottom-right {
    top: 135%;
    right: auto;
    margin-left:0;
    left: 0
  }
  .alph_tooltip-bottom-right::after {
    margin-left:0;
    right: auto;
    left: 15%;
  }
}

.alph_tooltip-left {
  top: 50%;
  transform: translateY(-50%);
  bottom: auto;
  right: 128%;
}

.alph_tooltip-left::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent var(--alpheios-border-color);
}
